.footer {
    width: 100%;
    margin-top: 5%;
    font-family: 'Times New Roman', Times, serif;
    line-height: 21px;
}

.upper-footer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.footer-logo {
    margin-bottom: 3%;
}

.footer-divider {
    width: 60%;
    border-top: 2px solid #bbb;
}

.lower-footer {
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
}

.dummyLinks > a {
    text-decoration: underline;
    padding: 15px;
    font-weight: 400;
    color: #000;
}

@media screen and (max-width: 768px){
    .footer {
        margin-top: 50px;
    }

    .lower-footer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .dummyLinks {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

}
